<template>
  <div class="paySuccess">
    <!--头部栏-->
    <!-- <van-nav-bar class="task-nav-bar" title="支付成功" :border="false" left-arrow @click-left="goBack" /> -->
    <navigation-bar  @onLeftClick="goBack" pageName="支付成功" />
    <div class="headerColor">
      <div class="headImg">
        <img src="@/assets/images/common/img_payment_successful.png" />
      </div>
      <div class="content">
        <div class="contentSecond"><span class="moneyIcon">￥</span><span
            class="moneyNum">{{orderDetail.realPayTotal}}</span></div>
        <div class="contentThird">
          <span v-if="orderDetail.pointIncome>0">恭喜获得<span>{{orderDetail.pointIncome}}</span>积分！</span><span
            style="color: #CE9D6A;text-decoration:underline" v-if="awardShow==0"
            @click='goToActivity()'>点此参与大转盘活动</span>
        </div>
      </div>
      <div class="finishDiv">
        <!--确认按钮-->
        <div class="finishButton" @click="finishCommit()">回到首页</div>
        <div class="finishButton seeOrder" @click="seeOrder()">查看订单</div>
      </div>
    </div>
    <!-- </ion-view> -->
  </div>



</template>

<script>

  import { mapState } from "vuex";

  import { selectConsumedService, orderDetailService } from '../../services/productOrderService.js';
  import NavigationBar from '@/common/currency/NavigationBar.vue';


  export default {
    name: 'paySuccess',
    components: {NavigationBar },
    data() {
      return {
        orderDetail: {},//订单信息
        promotion: {},//活动信息
        awardShow: 1,//是否显示转盘
        fromFlag: false,
      }
    },
    mounted() {

    },
    activated() {
      this.orderDetail = this.$route.params.orderData;
      this.getOrderDetail();
      this.getAreaActivity();
    },
    beforeRouteEnter(to, from, next) {


      next(vm => {
        if (from.path == "/orderPayNew") {
          vm.fromFlag = true;
        }
      })
    },
    computed: mapState({
      paysuccessEventBackButtonFlag: state => state.goBack.paysuccessEventBackButtonFlag
    }),
    watch: {



      /*监控到物理返回键点击状态*/
      paysuccessEventBackButtonFlag(val) {
        console.log("paysuccessEventBackButtonFlag")

        if (val) {
          console.log("eventBackButtonFlagpyesuccess");
          this.$store.dispatch('setPaysuccessEventBackButtonFlag', false);
          this.goBack();

        }
      }
    },
    methods: {


      //跳转订单详情
      seeOrder: function () {
        this.$router.push({ name: "myOrderList", query: { flag: 10,ifPayReturn:true} });

      },

      //回到首页
      finishCommit: function () {
        this.$router.push({ name: "productPage" });
      },


      //跳转活动转盘页面
      goToActivity: function () {
        this.$router.push({
          path: "/pointActivity",
          query: {
            promotionId: this.promotion.promotionId,
            title: this.promotion.promotionName,
            pointConsumed: this.promotion.pointConsumed
          }
        });

      },

      //获取小区活动
      getAreaActivity: function () {
        selectConsumedService(this.$userInfo.AREA_CODE).then(result => {
          this.awardShow = result.delFlag; // 0 显示
          this.promotion = result; //转盘活动id
        }).catch(error => {
        })
      },
      // 支付完成后获取订单详情查询订单状态
      getOrderDetail: function () {
        var data = {
          billNo: this.orderDetail.billNo,
          ownerCode: this.$userInfo.OWNER_CODE
        }
        orderDetailService(data).then(result => {
          if ("0" == result.resultCode) {//true表示返回成功
            console.log("支付页查看订单状态" + JSON.stringify(result));
            this.orderDetail = result.data;

          }
        }).catch(error => {
        })
      },

      goBack: function () {
        console.log("paysuccessback")
        console.log(this.fromFlag)

        if (this.fromFlag) {
          this.fromFlag = false;
          this.$router.go(-2);
        } else {
          this.$router.back();//返回上一层
        }

      },
    }
  }
</script>

<style>
  /*头部*/
  .paySuccess .headerColor {
    background: #ffffff;
    /*border-bottom: 1px solid #ECECEC;*/
    /* -moz-box-shadow: 0px 4px 4px #F0F0F4;
    -webkit-box-shadow: 0px 4px 4px #F0F0F4;
    box-shadow: 0px 4px 4px #F0F0F4; */
  }

  .paySuccess .bar .title {
    font-size: 18px;
  }

  .paySuccess .formStyle {
    display: block;
    margin-left: .75rem;
  }

  .paySuccess .headImg {
    padding-top: 101px;
    margin-bottom: 25px;
    text-align: center;
  }

  .headImg img {
    width: 90px;
    height: 112px;
  }

  .paySuccess .content {
    text-align: center;
  }

  .paySuccess .contentFirst {
    font-family: PingFangSC-Medium;
    font-size: 18px;
    color: #1A1A1A;
    letter-spacing: 0.07px;
    /*width: 80%;*/
    /*margin-left: 10%;*/
    text-align: center;
    height: 40px;
    line-height: 40px;
  }

  .paySuccess .contentSecond {
    text-align: center;
    height: 40px;
  }

  .contentSecond .moneyIcon {
    font-size: 18px;
    color: #3A3A3A;
    letter-spacing: 0.07px;
    line-height: 40px;
  }

  .contentSecond .moneyNum {
    font-size: 28px;
    color: #000000;
    letter-spacing: 0.07px;
    line-height: 40px;
  }

  .paySuccess .contentThird {
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #3a3a3a;
    letter-spacing: 0.06px;
    text-align: center;
    height: 40px;
    line-height: 40px;
  }

  .paySuccess .finishDiv {
    /* height: 40px; */
    display: flex;
    justify-content: space-between;
  }

  .paySuccess .finishButton {
    text-align: center;
    font-size: 16px;
    color: #FB5768;
    width: 32%;
    margin-left: 14%;
    margin-top: 40px;
    height: 44px;
    line-height: 42px;
    background: #FFFFFF;
    border: 1px solid #FB5768;
    border-radius: 22px;
  }

  .paySuccess .seeOrder {
    margin-right: 14%;
    margin-left: 0;
    background: #FB5768;
    box-shadow: 0 0 15px 0 rgba(197, 58, 72, 0.39);
    color: #FFFFFF !important;
    /* border: none !important; */
  }
</style>